@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
html,
body {
  margin: 0;
  padding: 0;
}
.App {
  display: flex;
  height: 100vh;
  overflow: hidden;
}
.content {
  height: 100vh;
  width: 87vw;
  overflow-y: auto;
}
.dummy-content {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.dummy-content > h2 {
  margin: 0;
  padding: 20px;
}

.sidebar {
  width: 13vw;
  height: 100vh;
  background-color: #e6e6e6;
}
.sidebar-content {
  height: 90vh;
  width: 100%;
  background-color: #595959;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
}
.route-link {
  font-family: "Lora", serif;
  text-decoration: none;
  color: white;
  border-bottom: 1px solid whitesmoke;
  margin: 0 15px;
  padding: 10px 0;
  transition: 0.5s ease-in-out;
}
.route-link:hover {
  color: bisque;
}
.router-link-active {
  color: aquamarine;
  padding: 10px 5px;
}
.router-link-active:hover {
  color: #70aa97;
}
.router-link-bottom {
  position: absolute;
  bottom: 5%;
  height: 80px;
  overflow-wrap: break-word;
  width: 10vw;
  border-bottom: none;
  border-top: 1px solid white;
}

.purchase-content {
  padding: 10px;
}
.content-heading {
  margin: 0;
  font-size: large;
  font-weight: bolder;
}
.purchase-tabs {
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-bottom: 1px solid gray;
  margin-top: 10px;
}
.purchase-tab {
  padding: 25px;
  padding-bottom: 4px;
  margin: 0 4px;
  font-size: 18px;
  background-color: #595959;
  color: white;
  text-decoration: none;
  cursor: pointer;
  transition: 0.5s ease-in-out;
  list-style: none;
}
.purchase-tab:hover {
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}
.tab-selected {
  background-color: #262626;
}
.purchase-tab::after {
  content: "";
  display: inline-block;
  height: 10px;
  width: 10px;
  margin-left: 12px;
  margin-bottom: 4px;
  border-right: 2px solid azure;
  border-bottom: 2px solid azure;
  transform: rotate(-45deg);
  transition: 0.5s ease-in-out;
}
.tab-selected::after {
  transform: rotate(45deg);
}

.product-modal {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  font-size: larger;
  height: 100%;
}
.product-image {
  width: auto;
  height: 20vh;
}
.product-content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: auto;
}

/* Executing Tab */

.dummy-button-group {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 5px 0;
}
.dummy-button {
  padding: 6px 30px;
  border-radius: 6px;
  font-size: 16px;
  font-family: "Lora", serif;
  margin: 0 10px;
  background-color: #595959;
  border: 2px solid #595959;
  color: white;
  cursor: pointer;
  outline: none;
  transition: 0.5s ease-in-out;
}
.dummy-button:hover {
  border: 2px solid gray;
}
.dummy-button:active {
  background-color: white;
  color: #595959;
}
.dummy-button-disabled {
  background-color: #d0cece;
  border: 1px solid #d0cece;
  cursor: not-allowed;
}
.dummy-button-disabled:hover {
  border: 1px solid #d0cece;
}
.dummy-button-disabled:active {
  color: white;
  background-color: #d0cece;
}

/* Orders Heading */

.orders-heading {
  width: 50%;
  display: flex;
  flex-direction: row;
  font-size: 18px;
  font-weight: bold;
  justify-content: space-around;
}

/* Order List - Accordion */

.accordion-item {
  width: 100%;
}
.accordion-button {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-width: 80%;
  background-color: #afabab;
  font-family: "Playfair Display", serif;
  padding: 10px 15px;
  outline: none;
  margin: 5px 0;
  font-weight: bold;
  transition: 0.5s ease-in-out;
}
.accordion-button:before {
  display: inline-block;
  content: "";
  height: 10px;
  width: 10px;
  margin-right: 12px;
  border-bottom: 2px solid currentColor;
  border-right: 2px solid currentColor;
  transform: rotate(-45deg);
}
.accordion-button[aria-expanded="true"]::before,
.accordion-button[aria-selected="true"]::before {
  transform: rotate(45deg);
}
.accordion-button-divider {
  font-size: 30px;
  color: white;
  padding: 0 10px;
  font-weight: bold;
}
.accordion-button-key {
  font-size: 18px;
}
.accordion-button-user {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.user-avatar {
  border: 1px solid #afabab;
  padding: 5px;
  margin-right: 5px;
  border-radius: 50%;
  transition: 0.5s ease-in-out;
}
.user-avatar:hover {
  box-shadow: 1px 2px 10px 2px gray;
}
.user-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
}
.accordion-button-csv {
  padding: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  transition: 0.5s ease-in-out;
}
.accordion-button-csv:hover {
  box-shadow: 1px 2px 10px 2px gray;
  border-radius: 4px;
}
.accordion-panel {
  padding: 10px 0;
  transition: 0.5s ease-in-out;
}

/* Purchase Table */

.products-table {
  border-collapse: collapse;
  width: 80%;
  text-align: center;
}
.products-table-head {
  background-color: black;
  color: white;
  font-size: 14px;
}
.products-table-head tr {
  padding-bottom: 5px;
  padding-top: 2px;
}
.products-table td,
.products-table th {
  border: 1px solid #ddd;
  padding: 8px;
}
.products-table-body tr {
  background-color: #cbcbcb;
  transition: 0.5s ease-in-out;
}
.products-table-body tr:nth-child(even) {
  background-color: #e7e7e7;
}
.products-table-body tr:hover {
  background-color: #262626;
  color: white;
}

.modal-popup {
  cursor: pointer;
  color: black !important;
}

